@-webkit-keyframes mymove {
    from {
        -webkit-transform: translate(0px);
                transform: translate(0px);
    }

    to {
        -webkit-transform: translate(-35px);
                transform: translate(-35px);
    }
}

@keyframes mymove {
    from {
        -webkit-transform: translate(0px);
                transform: translate(0px);
    }

    to {
        -webkit-transform: translate(-35px);
                transform: translate(-35px);
    }
}

